/* About.css */
#about {
  padding: 20px;
  text-align: center;
  background-color: #d8d8d8; /* Add your desired background color */
}

.section-title {
  font-size: 24px;
  margin-bottom: 20px;
  color: #333; /* Add your desired text color */
}

.about__subtitle {
  font-size: 18px;
  margin-top: 10px;
  color: #555; /* Add your desired text color */
}

.about__text {
  font-size: 16px;
  margin: 20px 0;
  color: #110808; /* Add your desired text color */
}

.about__img {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 20px;
  font-style:italic;
  font-size:x-large;
}

.about__img img {
  max-width: 100px; /* Set your desired image width */
  max-height: 100px; /* Set your desired image height */
  margin: 0 10px; /* Add some spacing between images */
}

.about__img {
  padding: 20px;
}

.logo1 {
  border-radius: 50px;
  margin-left: 50px;
}

.logo2 {
  border-radius: 8px;
  height: 80px;
  margin-left: 50px;
}
.logo3 {
  border-radius: 10px;
  height: 80px;
  margin-right: 10px;
}
