.projects.section {
  text-align: center;
  background-color: #d8d8d8;
  padding: 20px;
} 

/* Default styles for work__container and work__card */
.work__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap; /* Allow cards to wrap to the next row on smaller screens */
}

.work__card{
  flex: 0 0 calc(50% - 20px); /* Make cards take up 50% of the container width with margin */
  margin: 10px;
  height: auto;
  max-width: 400px; /* Set a maximum width for cards */
}

/* Styles for work__img */
.work__img {
  max-width: 100%;
  height: auto;
  border: 1px solid #ddd;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

/* Media queries for responsiveness */
@media (min-width: 768px) {
  .work__card,
  .work__card2 {
    flex: 0 0 calc(33.333% - 20px); /* Make cards take up 33.333% of the container width with margin */
  }
}

@media (min-width: 1024px) {
  .work__card,
  .work__card2 {
    flex: 0 0 calc(25% - 20px); /* Make cards take up 25% of the container width with margin */
  }
}
