/* Contact.css */

.contact__container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top: 20px;
  }
  
  .contact__form {
    display: flex;
    flex-direction: column;
    max-width: 400px; /* Adjust the maximum width of the form */
    width: 100%;
  }
  
  .contact__input {
    padding: 10px;
    margin-bottom: 20px; /* Adjust the spacing between form fields */
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 16px;
  }
  
  .contact__button {
    background-color: #007BFF; /* Change the button background color */
    color: #fff; /* Change the button text color */
    font-size: 16px;
    padding: 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .contact__button:hover {
    background-color: #0056b3; /* Change the button background color on hover */
  }
  