/* Header.css */

header {
    background-color: 
    #3e4349; /* Header background color */
    color: #fff; /* Text color */
    padding: 20px 0;
    text-align: center;
  }
  
  h1 {
    font-size: 36px; /* Adjust the header text size */
    margin: 0;
  }
  
  nav ul {
    list-style: none;
    padding: 0;
    margin-top: 20px; /* Adjust the spacing between the header and navigation */
  }
  
  nav li {
    display: inline;
    margin-right: 20px; /* Adjust spacing between navigation items */
  }
  
  nav a {
    text-decoration: none;
    color: #fff; /* Navigation link color */
    font-size: 18px; /* Adjust the font size of navigation links */
  }
  
  nav a:hover {
    text-decoration: underline; /* Underline navigation links on hover */
  }
  
  