/* Skills.css */
.skills {
  padding: 80px 0;
  background-color: #d8d8d8;
}

.section-title {
  font-size: 32px;
  text-align: center;
  margin-bottom: 40px;
}

.skills__container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
}

.skills__left {
  flex: 1;
  padding-right: 30px;
}

.skills__subtitle {
  font-size: 24px;
  margin-bottom: 20px;
}

.skills__text {
  font-size: 16px;
  line-height: 1.6;
  margin-bottom: 30px;
  color:rgb(39, 35, 34);
}

.skills__data {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}

.skills__item {
  flex-basis: calc(50% - 20px);
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.skills__icon {
  font-size: 24px;
  margin-right: 10px;
}

.skills__name {
  font-size: 18px;
  font-weight: bold;
}

.skills__bar {
  background-color: #999595;
  height: 10px;
  border-radius: 5px;
  width: 100%;
  position: relative;
}

.skills__percentage {
  position: absolute;
  right: 5px;
  top: 50%;
  transform: translateY(-50%);
  font-size: 14px;
  font-weight: bold;
}


.skills__html {
  background-color: #c94e19;
}

.skills__css {
  background-color: #962e6b;
}

.skills__js {
  background-color: #a7941c;
}

.skills__python {
  background-color: #3c719c;
}

.skills__java {
  background-color: #743b15;
}

.skills__linux {
  background-color: #b87c1c;
}

.skills__git {
  background-color: rgb(27, 66, 53);
}

.skills__img {
  max-width: 100%;
  height: auto;
}

.anjali {
  flex: 1;
}

.anjali_img {
  display: block;
  margin: 0 auto;
  max-width: 100%;
}
