/* Base styles for profile section */
.profile {
  background-color: #d8d8d8;
  padding: 20px;
  text-align: center;
  flex-wrap: wrap;
}

.section-title {
  font-size: 24px;
  margin-bottom: 20px;
}

.profile-info {
  display: flex;
  flex-direction: column; /* Stack elements vertically on smaller screens */
  align-items: center;
  text-align: center;
}

.profile-text {
  text-align: center; /* Center text on all screen sizes */
}

.title {
  font-size: 20px;
  margin-bottom: 10px;
}

.title_name {
  font-weight: bold;
}

.pro {
  width: 20%;
  height: 25%;
  overflow: hidden;
  border-radius: 40%;
  border: 1px solid #333;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
  margin-top: 20px; /* Add margin at the top */
}

.pro img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.home__social {
  margin-top: 20px;
  text-align: center;
}

.home__social-icon {
  font-size: 24px;
  margin-right: 10px;
  color: #333;
}

/* Add hover effects for social icons if desired */
.home__social-icon:hover {
  color: #007bff;
}

/* Responsive adjustments */
@media (min-width: 768px) {
  .profile-info {
    flex-direction: row; /* Revert to horizontal layout on larger screens */
    justify-content: space-between; /* Spread elements horizontally */
    align-items: center;
    text-align: left; /* Align text to the left */
   
  }

  .title {
    margin: 0; /* Remove margin for title */
  }

  .pro {
    margin-top: 0; /* Remove top margin for the image */
  }
}
