/* Hobbies.css */

.work__container {
    display: fixed; 
   
    
}

.work__img {
    flex: 0 0 auto;
    margin-right: 20px; /* Adjust spacing between cards */
    max-width: 300px; /* Adjust the maximum width of each card */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Add shadow to the cards */
    border-radius: 10px; /* Add border-radius for rounded corners */
    overflow: hidden; 
}

.work__img img {
    width: 19vw;
    height: 27vh;
}
