.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(360deg);
  }
  to {
    transform: rotate(360deg);
  }
}
/* Reset some default styles for consistency */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Apply a background color and font for the entire app */
body {
  font-family: Arial, sans-serif;
  background-color: #f5f5f5;
  padding-left: 150px;
  padding-right: 150px;
  max-width: 60000px;
  padding:10px;
}

/* Style the container for the entire app */
.App {
  margin: 10px;
  max-width: 15500px;
}

/* Style the header */
header {
  background-color: #333;
  color: #fff;
  padding: 1rem 0;
  text-align: center;
}

/* Style the navigation links in the header */
header ul {
  list-style-type: none;
  margin-top: 10px;
}

header ul li {
  display: inline;
  margin-right: 20px;
}

header a {
  text-decoration: none;
  color: #fff;
  font-weight: bold;
}

/* Style the sections */
section {
  padding: 20px;
  background-color: #d8d8d8;
  margin: 20px 0;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  section: 140%;
}

/* Style the h1 headers in sections */
section h1 {
  color: #333;
  font-size: 2rem;
  margin-bottom: 20px;
}

/* Style the paragraphs in sections */
section p {
  color: #555;
  font-size: 1.2rem;
  line-height: 1.6;
}

/* Style the footer */
footer {
  text-align: center;
  padding: 1rem 0;
  background-color: #333;
  color: #fff;
}
.tittle {
  margin-right: 500px;
}
.tittle_name {
  color: #0e5468;
}
.head {
  text-align: left;
}

/* Profile.css */

.section-title {
  color: #0e5468;
  font-size: 30px;
}
